<template>
  <div>
    <Button :size="buttonSize" icon="ios-download-outline" type="primary" @click="download"
      >Download</Button
    >
  </div>
</template>
<script>
/* eslint-disable */
export default {
  data() {
    return {
      buttonSize: "large",
    };
  },
  methods:{
      download:function(){
          console.log("xxxxxxx")
          let a = document.createElement('a')
          let url = "/static/2022.zip"
          let filename = '2022.zip'
          a.href = url
          a.download = filename
          a.click()
          console.log("oooooooooo")
      }
  }
};
</script>



