import Vue from 'vue'
import Router from 'vue-router'
import DownloadButton from '@/components/DownloadButton'

/* eslint-disable */
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'DownloadButton',
      component: DownloadButton
    }
  ]
})



